"use client"

import { useState, useEffect, useRef } from 'react'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Card, CardContent, CardHeader } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { ZoomIn, ZoomOut, Home, LineChart } from 'lucide-react'
import * as echarts from 'echarts'

const generateTimeLabels = () => {
  const labels = []
  const data1 = []
  const data2 = []
  const data3 = []
  
  for (let hour = 9; hour <= 15; hour++) {
    for (let minute = 0; minute < 60; minute += 15) {
      const time = `${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`
      labels.push(time)
      data1.push([time, 470 + Math.random() * 50])
      data2.push([time, 12550 + Math.random() * 100])
      data3.push([time, 12580 + Math.random() * 100])
    }
  }
  return { labels, data1, data2, data3 }
}

export default function ModernChart() {
  const [selectedIndex, setSelectedIndex] = useState("MIDCAPNIFTY")
  const [selectedStrike, setSelectedStrike] = useState("12575") // Default strike price
  const chartRef = useRef<HTMLDivElement>(null)
  const chartInstance = useRef<echarts.ECharts | null>(null)
  
  const { labels, data1, data2, data3 } = generateTimeLabels()

  const handleChartUpdate = () => {
    // Here you would typically fetch new data based on selectedIndex and selectedStrike
    console.log("Updating chart with:", selectedIndex, selectedStrike)
  }

  useEffect(() => {
    if (!chartRef.current) return

    chartInstance.current = echarts.init(chartRef.current)

    const option = {
      tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgba(255, 255, 255, 0.9)',
        borderColor: 'rgba(0, 0, 0, 0.1)',
        borderWidth: 1,
        padding: 12,
        textStyle: {
          color: '#1a1a1a'
        },
        axisPointer: {
          type: 'cross',
          label: {
            backgroundColor: '#6a7985'
          }
        }
      },
      legend: {
        data: ['Straddle Price', 'Index Spot', 'Synthetic Future'],
        bottom: 0,
        padding: 20,
        icon: 'circle'
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '12%',
        top: '3%',
        containLabel: true
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        axisLine: { show: false },
        axisTick: { show: false },
        axisLabel: {
          rotate: 0,
          showMaxLabel: true
        },
        data: labels
      },
      yAxis: [
        {
          type: 'value',
          position: 'right',
          splitLine: {
            lineStyle: {
              color: 'rgba(0, 0, 0, 0.05)'
            }
          }
        }
      ],
      dataZoom: [
        {
          type: 'inside',
          start: 0,
          end: 100
        }
      ],
      series: [
        {
          name: 'Straddle Price',
          type: 'line',
          smooth: true,
          symbol: 'none',
          lineStyle: {
            width: 2,
            color: 'rgb(59, 130, 246)'
          },
          data: data1
        },
        {
          name: 'Index Spot',
          type: 'line',
          smooth: true,
          symbol: 'none',
          lineStyle: {
            width: 2,
            color: 'rgb(16, 185, 129)'
          },
          data: data2
        },
        {
          name: 'Synthetic Future',
          type: 'line',
          smooth: true,
          symbol: 'none',
          lineStyle: {
            width: 2,
            color: 'rgb(249, 115, 22)'
          },
          data: data3
        }
      ]
    }

    chartInstance.current.setOption(option)

    const handleResize = () => {
      chartInstance.current?.resize()
    }

    window.addEventListener('resize', handleResize)

    return () => {
      window.removeEventListener('resize', handleResize)
      chartInstance.current?.dispose()
    }
  }, [selectedIndex, selectedStrike])

  const handleZoomIn = () => {
    if (!chartInstance.current) return
    const option = chartInstance.current.getOption()
    const dataZoom = option.dataZoom?.[0] as { start: number; end: number }
    if (!dataZoom) return
    
    const range = dataZoom.end - dataZoom.start
    const center = (dataZoom.start + dataZoom.end) / 2
    const newRange = range * 0.8
    
    chartInstance.current.setOption({
      dataZoom: [{
        start: Math.max(0, center - newRange / 2),
        end: Math.min(100, center + newRange / 2)
      }]
    })
  }

  const handleZoomOut = () => {
    if (!chartInstance.current) return
    const option = chartInstance.current.getOption()
    const dataZoom = option.dataZoom?.[0] as { start: number; end: number }
    if (!dataZoom) return
    
    const range = dataZoom.end - dataZoom.start
    const center = (dataZoom.start + dataZoom.end) / 2
    const newRange = range * 1.2
    
    chartInstance.current.setOption({
      dataZoom: [{
        start: Math.max(0, center - newRange / 2),
        end: Math.min(100, center + newRange / 2)
      }]
    })
  }

  const handleReset = () => {
    if (!chartInstance.current) return
    chartInstance.current.setOption({
      dataZoom: [{
        start: 0,
        end: 100
      }]
    })
  }

  // Mock strike prices - in real app, these would come from backend
  const strikeOptions = ["12525", "12550", "12575", "12600", "12625"]

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 p-8">
      <Card className="w-full max-w-6xl mx-auto backdrop-blur-xl bg-white/80 border border-gray-200/50 shadow-xl">
        <CardHeader className="border-b border-gray-100">
          <div className="flex justify-between items-center flex-wrap gap-4">
            <div className="flex items-center gap-4 flex-wrap">
              <div className="flex items-center gap-2">
                <span className="text-sm font-medium text-gray-500">Index</span>
                <Select value={selectedIndex} onValueChange={setSelectedIndex}>
                  <SelectTrigger className="w-[180px] bg-white">
                    <SelectValue placeholder="Select index" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="MIDCAPNIFTY">MIDCAPNIFTY</SelectItem>
                    <SelectItem value="NIFTY">NIFTY</SelectItem>
                    <SelectItem value="BANKNIFTY">BANKNIFTY</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="flex items-center gap-2">
                <span className="text-sm font-medium text-gray-500">Strike</span>
                <Select value={selectedStrike} onValueChange={setSelectedStrike}>
                  <SelectTrigger className="w-[120px] bg-white">
                    <SelectValue placeholder="Select strike" />
                  </SelectTrigger>
                  <SelectContent>
                    {strikeOptions.map((strike) => (
                      <SelectItem key={strike} value={strike}>
                        {strike}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <Button 
                variant="default" 
                size="default" 
                onClick={handleChartUpdate}
                className="flex items-center gap-2"
              >
                <LineChart className="h-4 w-4" />
                Chart
              </Button>
            </div>
            <div className="flex gap-2">
              <Button variant="outline" size="icon" onClick={handleZoomIn}>
                <ZoomIn className="h-4 w-4" />
              </Button>
              <Button variant="outline" size="icon" onClick={handleZoomOut}>
                <ZoomOut className="h-4 w-4" />
              </Button>
              <Button variant="outline" size="icon" onClick={handleReset}>
                <Home className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent className="p-6">
          <div className="h-[500px]" ref={chartRef} />
          <div className="mt-6 grid grid-cols-3 md:grid-cols-6 gap-4 p-4 bg-gray-50/50 rounded-lg">
            <div className="space-y-1">
              <div className="text-sm text-gray-500">Straddle Price</div>
              <div className="text-lg font-semibold">473</div>
            </div>
            <div className="space-y-1">
              <div className="text-sm text-gray-500">MIDCAPNIFTY Spot</div>
              <div className="text-lg font-semibold">12563.6</div>
            </div>
            <div className="space-y-1">
              <div className="text-sm text-gray-500">Synthetic Future</div>
              <div className="text-lg font-semibold">12598.00</div>
            </div>
            <div className="space-y-1">
              <div className="text-sm text-gray-500">Straddle Strike</div>
              <div className="text-lg font-semibold">{selectedStrike}</div>
            </div>
            <div className="space-y-1">
              <div className="text-sm text-gray-500">Days to Expiry</div>
              <div className="text-lg font-semibold">22</div>
            </div>
            <div className="space-y-1">
              <div className="text-sm text-gray-500">Last Updated</div>
              <div className="text-lg font-semibold">03:30 PM</div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
